import {get,showToast} from '@/utils/index';
import {getBookDescUrl} from '@/config';
export default{
	data(){
		return{
			id: '', //小说id
			dataObj: {}
		}
	},
	mounted(){
		this.id = this.$mp.query.id;
		this.getData();
	},
	methods: {
		//设置该小说
		jumpPage(url){
			console.log(url);
			if(url && typeof(url)=='string'){
				uni.navigateBack();
				uni.navigateTo({
					url: url,
					animationType: 'zoom-out',
					success(res) {
					}
				});
			}
		},
		getData(){
			if(!this.id){
				return;
			};
			let url = getBookDescUrl + '/'+this.id;
			get(url, {})
			.then((res)=>{
				res.cover = decodeURIComponent(res.cover).replace('/agent/', '');
				this.dataObj = res;
			});
		},
		//点击添加
		addBook(){
			let tmp ={
				id: this.id,
				cover: this.cover,
				title: this.dataObj.title
			};
			this.$store.commit('novel/ADD_BOOK', tmp);
		}
	},
	onPullDownRefresh() {
		uni.stopPullDownRefresh();
		uni.hideNavigationBarLoading();
		this.getData();
	}
}
